.App {
  text-align: center;
  margin-top: 20px;
}

.game-container {
  position: relative;
  display: inline-block;
}

.score {
  font-size: 24px;
  color: #333;
  margin-bottom: 10px;
}

.board {
  display: inline-block;
  margin-top: 20px;
}

.row {
  display: flex;
}

.cell {
  width: 40px; /* Изменённый размер ячеек */
  height: 40px;
  border: 1px solid #ddd;
  position: relative;
}

.snake {
  background: linear-gradient(145deg, #228b22, #32cd32);
  border-radius: 15%;
  box-shadow: inset -2px -2px 5px rgba(0, 0, 0, 0.2);
  position: relative;
}

.snake::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 15%;
  background: rgba(255, 255, 255, 0.1);
  top: 0;
  left: 0;
}

.food {
  position: relative;
  background-image: url('C:\Users\Egor\Desktop\telega\telega-app\public\32349redapple_98912.png'); /* Путь к изображению яблока без фона */
  background-size: contain; /* Сохраняем пропорции изображения */
  background-position: center;
  background-repeat: no-repeat; /* Отключаем повторение изображения */
  border-radius: 50%; /* Сглаживаем края */
}

.bonus {
  background-color: white;
}

.cell.bonus {
  background-image: url('C:\Users\Egor\Desktop\telega\telega-app\public\present_bonus_icon-icons.com_53587.png'); /* Путь к вашему изображению бонуса */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.game-over {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: red;
  font-size: 24px;
}

.restart-button {
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.restart-button:hover {
  background-color: #218838;
}

.difficulty-selector {
  margin: 20px;
  text-align: center;
}

.difficulty-selector h2 {
  margin-bottom: 10px;
}

.difficulty-selector button {
  padding: 10px 20px;
  margin: 5px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  background-color: #007bff;
  color: white;
}

.difficulty-selector button:hover {
  background-color: #0056b3;
}

.high-score {
  font-size: 20px;
  margin-bottom: 20px;
  font-weight: bold;
  color: #4CAF50; /* Цвет для рекорда */
}

/* Стиль для препятствий */
.cell.obstacle {
  background-color: #808080; /* Цвет препятствия */
  background-image: url('C:\Users\Egor\Desktop\telega\telega-app\public\four-squares-in-a-square-outlined-symbol-of-layout-visualization-option_icon-icons.com_74282.png'); /* Если хотите использовать изображение препятствия */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
